<template>
  <div>
    <el-card>
      <!---->
      <el-row :gutter = "20">
        <el-col :span = "8">
          <el-input clearable @clear = "getOrderList" @keydown.enter.native = "queryOrderList" placeholder = ""
                    v-model = "queryInfo.query">
            <el-button @click = "queryOrderList" slot = "append" icon = "el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data = "orderList" border stripe max-height = "500">
        <el-table-column type = "index" label = "#"></el-table-column>
        <el-table-column label = "" prop = "order_number"></el-table-column>
        <el-table-column label = "" prop = "order_price"></el-table-column>
        <el-table-column label = "" v-slot="scope">
          <el-tag type="danger" v-if="scope.row.pay_status==='0'"></el-tag>
          <el-tag type="success" v-else></el-tag>
        </el-table-column>
        <el-table-column label = "" prop = "is_send"></el-table-column>
        <el-table-column label = "" prop = "create_time"></el-table-column>
        <el-table-column label = "" >
          <el-tooltip effect = "dark" content = "" placement = "top" :enterable = "false">
            <el-button @click = "showEditDialog" type = "primary" icon = "el-icon-edit"
                       size = "mini"></el-button>
          </el-tooltip>
          <el-tooltip effect = "dark" content = "" placement = "top" :enterable = "false">
            <el-button @click="showProgressBox" type = "success" icon = "el-icon-location-outline" size = "mini"></el-button>
          </el-tooltip>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change = "handleSizeChange"
        @current-change = "handleCurrentChange"
        :current-page = "queryInfo.pagenum"
        :page-sizes = "[4, 6, 8, 10]"
        :page-size = "queryInfo.pagesize"
        layout = "sizes,total,prev, pager, next, jumper"
        :total = "total">
      </el-pagination>
    </el-card>
    <!---->
    <el-dialog
      title = ""
      :visible.sync = "addressVisable"
      width = "50%"
      @close = "editDialogClosed">
      <el-form :model = "editForm" :rules = "editFormRules" ref = "editFormRef" label-width = "70px">
        <el-form-item label = "/" prop = "address1" label-width="100px">
          <el-cascader :options="cityData" v-model="editForm.address1"
                       :props="{ expandTrigger: 'hover' }"></el-cascader>
        </el-form-item>
        <el-form-item label = "" prop = "address2" label-width="100px">
          <el-input v-model = "editForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot = "footer" class = "dialog-footer">
        <el-button @click = "addressVisable = false"> </el-button>
        <el-button type = "primary" @click = "editOrderInfo"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title = ""
      :visible.sync = "progressVisable"
      width = "50%">
      <!---->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in wuLiu"
          :key="index"
          :color="activity.color"
          :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from '@/components/order/citydata'
import wuLiu from '@/components/order/wuLiu'
export default {
  name: 'Order',
  data() {
    return {
      orderList: [],
      // 
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 8
      },
      total: 0,
      // 
      addressVisable: false,
      // 
      editForm: {
        address1: [],
        address2: ''
      },
      editFormRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cityData,
      wuLiu,
      progressVisable: false,
      // 
      progressInfo: []
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      } else {
        this.orderList = res.data.goods
        this.total = res.data.total
      }
    },
    // 
    queryOrderList() {
      this.queryInfo.pagenum = 1
      this.getOrderList()
      this.queryInfo.query = ''
    },
    // pageSize
    handleSizeChange(newSize) {
      // console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    //   
    handleCurrentChange(newPage) {
      // console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    async showEditDialog() {
      // 
      //  / - ElementUI el-cascader 
      this.addressVisable = true
      // console.log(id)
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editOrderInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) {
          this.$message.error('')
          return
        }
        // 
        // / - ElementUI el-cascader 
        this.$message.success(' /')
        // 
        this.editDialogVisable = false
      })
    },
    async showProgressBox() {
      this.progressVisable = true
    }
  }
}
</script>

<style lang="less" scoped>
.el-cascader{
  width: 100%;
}
</style>
